*{--fluid-size: var(--min-font-size) * 1px + (var(--max-font-size) - var(--min-font-size)) * ((100vw - 420px) / (1200 - 420))}h1{font-family:'Special Elite', cursive;color:#0ccac4;--minFontSize: 28px;--maxFontSize: 100px;--scaler: 10vw;font-size:clamp(var(--minFontSize), var(--scaler), var(--maxFontSize))}body{margin:0;padding:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:linear-gradient(to left, #071b52 0%, teal 100%)}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}body,h1,h2,h3,h4,h5,h6,ul,ol,li,p,pre,blockquote,figure,hr{margin:0;padding:0}

.card-container {
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  background-color: #95dada;
  border: 1px solid grey;
  border-radius: 5px;
  /*padding: 15px;*/
  cursor: pointer;
  -moz-osx-font-smoothing: grayscale;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.card-container img {
  width: 100%;
  background-size: cover;
  background-position: center;
  margin-bottom: 5px;
}

.card-container:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.card-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(20rem, 100%), 1fr));
  grid-gap: 20px;
  gap: 20px;
}

.search {
  -webkit-appearance: none;
  border: none;
  outline: none;
  padding: 10px;
  width: 200px;
  line-height: 30px;
  margin-bottom: 10px;
}

.App {
  text-align: center;
}

.App h1{
  margin: 10px 0;
}

